<template>
  <div class="jumpMini">
    <img
      src="@/assets/JumpMini/bg.png"
      alt=""
      class="bg" />
    <img
      src="@/assets/JumpMini/btn.png"
      alt=""
      class="btn"
      @click="jumpMini" />
  </div>
</template>
<script>
import { getSearch } from '@/utils'
export default {
  created () {
    const { protocol, host, link, ...query } = getSearch()
    const uid = this.getUid()
    this.$log.show({
      module_ori: '基于页面',
      module_part: '106_tf_wxxcx_00076',
      alternate: JSON.stringify({ ...query, pageName: '微淼李老师', uid_recall: uid })
    })
  },
  methods: {
    async jumpMini () {
      const { protocol, host, link, ...query } = getSearch()
      const uid = this.getUid()
      this.$log.click({
        module_ori: '点击事件',
        module_part: '106_tf_wxxcx_00077',
        alternate: JSON.stringify({ ...query, pageName: '微淼李老师', uid_recall: uid })
      })
      const { url } = await this.$apis.userOperator.getMiniHref({
        path: 'pages/officalCode/index',
        appid: 'wxdd51b8f4efa83b83',
        query: { ...query, uid_recall: uid }
      })
      window.location.href = url
    },
    getUid () {
      let uid = localStorage.getItem('uid_recall')
      if (!uid) {
        uid = getSearch('wm_click_id')
        localStorage.setItem('uid_recall', uid)
      }
      return uid
    }
  }
}
</script>
<style lang="scss" scoped>
.jumpMini {
  position: relative;
  min-height: 500px;
  .btn {
    position: absolute;
    z-index: 10;
    width: 358px;
    bottom: 25%;
    left: 50%;
    transform: translateX(-50%);
  }
  .bg {
    width: 100%;
  }
}
</style>
